html{
    scroll-behavior: smooth;
}
.containerBody{
width: 95%;
margin: auto;

padding-top: 1rem;
display: grid;
grid-gap: 2rem;
padding: 2rem 0rem;
grid-template-columns: repeat(auto-fit,minmax(21rem,1fr));
grid-template-rows: repeat(auto-fit,minmax(14rem,16rem));
background-color: yellow;
}
.rectangle{
    display: flex;
    flex-direction: row;
    height: 15rem;
    color: rgb(240, 228, 228);
    padding: .7rem .4rem;
    background-color: rgb(39, 34, 34);
    flex-direction: column;
    text-align: center;
    font-size: 13px;
}
.rectangle p{
    margin-top: .5rem;
}
.img{
    height: 12rem;
    /* padding: 1rem;
    padding-top: 8px; */
}
@media (max-width: 50rem) and (min-width: 30rem){
    .containerBody{
    grid-template-columns: repeat(auto-fit,minmax(16rem,1fr));
    }
}